<template>
    <!-- Choice -->
    <section class="choice">
        <div class="container">
            <div class="col-lg-12">
                <div class="text-center mb-0 animated fadeIn__1">
                    <img class="choice-img animated fadeIn" src="../assets/3d_language.png" alt="choice" />
                </div>
                <div class="mb-3 text-center text-lg-start">
                    <h1 class="display-5 lh-1 mb-1 text-center animated fadeIn__2">어떤 언어를 배워보시겠어요?</h1>
                </div>
                <p class="lead fw-normal text-muted text-center animated fadeIn__3">배우고 싶은 언어를 선택해주세요.</p>
            </div>
        </div>
    </section>

    <!-- Languages -->
    <section class="language">
        <div class="container mt-0 mb-0">
            <div class="row ms-4 me-4">
                <div
                    @click="
                        showModal = true;
                        clicked = langs[i].id;
                    "
                    v-for="(a, i) in langs"
                    :key="i"
                    class="col-sm-12 col-md-6 col-xl-3 mb-3"
                >
                    <article class="white-box">
                        <div class="container-box col-12 text-center">
                            <div class="box">
                                <span :style="{ '--i': 1, color: langs[i].color1 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 2, color: langs[i].color2 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 3, color: langs[i].color1 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 4, color: langs[i].color2 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 5, color: langs[i].color1 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 6, color: langs[i].color2 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 7, color: langs[i].color1 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 8, color: langs[i].color2 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 9, color: langs[i].color1 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 10, color: langs[i].color2 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 11, color: langs[i].color1 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 12, color: langs[i].color2 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 13, color: langs[i].color1 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 14, color: langs[i].color2 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 15, color: langs[i].color1 }">{{ langs[i].lang }}</span>
                                <span :style="{ '--i': 16, color: langs[i].color2 }">{{ langs[i].lang }}</span>
                            </div>
                        </div>
                        <div class="language-desc col-12 text-center">
                            <p class="language-info__title">{{ langs[i].lang }}</p>
                            <p class="language-info__desc">{{ langs[i].desc }}</p>
                        </div>
                    </article>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import Langs from '../assets/strings/languages.js';

export default {
    name: 'LanguageView',
    data() {
        return {
            langs: Langs,
        };
    },
    components: {},
};
</script>

<style>
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/*
 * ------------------------------
 * Choice
 * ------------------------------
 */
.choice {
    background-color: #fff;
    padding-top: 5rem;
    padding-bottom: 1rem;
}

.choice h1 {
    font-weight: bold;
}

.choice-img {
    height: 200px;
}

/*
 * ------------------------------
 * Language
 * ------------------------------
 */
.language {
    background-color: #fff;
    padding-top: 1rem;
    padding-bottom: 5rem;
}

.language-img img {
    height: 150px;
}

.language-info__title {
    color: #303030;
    font-weight: bold;
    font-size: 30px;
    margin-top: 5%;
    margin-bottom: 5px;
    font-family: 'Montserrat', 'Secular One', sans-serif;
}

.language-info__desc {
    color: #888;
    font-weight: 500;
    font-size: 20px;
}

@media only screen and (max-width: 800px) {
    .language-info__title {
        font-size: 26px;
    }

    .language-info__desc {
        font-size: 16px;
    }
}

/*
 * ------------------------------
 * Rotating Texts
 * ------------------------------
 */
.container-box {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 200px;
    margin-bottom: 200px;
}

.container-box .box {
    transform-style: preserve-3d;
    animation: animate 20s linear infinite;
}

@keyframes animate {
    0% {
        transform: perspective(1000px) rotateX(0deg) rotate(30deg);
    }
    100% {
        transform: perspective(1000px) rotateX(360deg) rotate(30deg);
    }
}

.container-box .box span {
    font-family: 'Montserrat', 'Secular One', sans-serif;
    position: absolute;
    color: #303030;
    font-size: 1.6em;
    white-space: nowrap;
    text-transform: uppercase;
    font-weight: 900;
    line-height: 0.76em;
    transform-style: preserve-3d;
    transform: translate(-50%, -50%) rotateX(calc(var(--i) * 22.5deg)) translateZ(100px);
    /* 360/16 = 22.5deg */
}

@media only screen and (max-width: 800px) {
    .container-box .box span {
        font-size: 2em;
        line-height: 0.76em;
    }
}

/*
 * ------------------------------
 * Transition
 * ------------------------------
 */
.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.4s linear;
}

.fade-enter {
    opacity: 0;
}

.fade-enter-active {
    transition: opacity 0.4s linear;
    opacity: 1;
}

.fade-leave {
    opacity: 1;
}

.fade-leave-active {
    transition: opacity 0.4s linear;
    opacity: 0;
}
</style>
