<template>
    <header>
        <nav class="navbar navbar-expand-lg navbar-light fixed-top shadow-sm" id="mainNav">
            <div class="container">
                <a class="logo" href="/"><img class="logo__img" src="@/assets/nomadika_logo_dark.png" alt="logo" /></a>
                <ul class="nav__links">
                    <li><router-link to="/about">About</router-link></li>
                    <li><router-link to="/language">Language</router-link></li>
                    <li><a href="#">Life Overseas</a></li>
                    <li><a href="#">Digital Nomad</a></li>
                    <li><a href="#">Business</a></li>
                    <li><a href="#">Blog</a></li>
                </ul>
                <a class="login" href="#">로그인</a>
                <button class="menu cta" title="Menu" type="button">
                    <span class="icon menu-toggle" aria-hidden="true"><img src="@/svg/menu-toggle-w.svg" /></span>
                </button>
            </div>
        </nav>
    </header>
    <div id="mobile__menu" class="overlay">
        <a class="close">&times;</a>
        <div class="overlay__content">
            <a class="logo" href="/"><img class="logo__img" src="@/assets/nomadika_logo_dark.png" alt="logo" /></a>
            <router-link to="/about">About</router-link>
            <router-link to="/language">Language</router-link>
            <a href="#">Life Overseas</a>
            <a href="#">Digital Nomad</a>
            <a href="#">Business</a>
            <a href="#">Blog</a>
        </div>
        <div class="login__wrap">
            <a class="login__overlay" href="#" style="font-size: 16px">로그인</a>
        </div>
    </div>
</template>

<script>
export default {
    name: 'HeaderVue',
    data() {
        return {};
    },
    methods: {},
    mounted() {
        const doc = document;
        const menuOpen = doc.querySelector('.menu');
        const menuClose = doc.querySelector('.close');
        const overlay = doc.querySelector('.overlay');

        menuOpen.addEventListener('click', () => {
            overlay.classList.add('overlay--active');
        });

        menuClose.addEventListener('click', () => {
            overlay.classList.remove('overlay--active');
        });

        // document.window.scroll(function () {
        //     var scTop = document.window.scrollTop();
        //     console.log(scTop);

        //     const nav = doc.querySelector('.navbar');
        //     if (scTop >= 150) {
        //         nav.classList.add('navScroll');
        //     } else {
        //         nav.classList.remove('navScroll');
        //     }
        // });
    },
};
</script>

<style>
header {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 10px 5%;
    background-color: #fff;
}

#mainNav {
    padding-top: 0.6rem;
    padding-bottom: 0.6rem;
    background-color: rgba(255, 255, 255, 1);
    z-index: 1;
}

.logo__img {
    margin-right: auto;
    width: 125px;
    height: 25px;
    transition: all 0.3s;
}

.logo__img:hover {
    content: url('../assets/nomadika_logo_gold.png');
}

.nav__links {
    list-style: none;
    display: flex;
}

.nav__links a,
.cta,
.overlay__content a {
    font-family: 'Lato', sans-serif;
    font-weight: 500;
    color: #303030;
    text-decoration: none;
}

.nav__links li {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
}

.nav__links li a {
    transition: all 0.3s ease 0s;
}

.nav__links li a:hover {
    color: #fec502;
}

.cta {
    margin-left: 20px;
    padding: 6px 8px;
    background-color: rgba(254, 197, 2, 1);
    border: none;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s ease 0s;
}

.cta:hover {
    background-color: rgba(254, 197, 2, 0.7);
}

.login {
    margin-left: 20px;
    padding: 8px 16px;
    background-color: rgba(254, 197, 2, 1);
    border: none;
    border-radius: 10px;
    color: #fff;
    cursor: pointer;
    transition: all 0.3s ease 0s;
    text-decoration: none;
    font-family: 'Noto Sans KR', sans-serif;
    font-weight: 700;
}

.login:hover {
    background-color: rgba(255, 255, 255, 1);
    box-shadow: rgba(254, 197, 2, 0.3) 0px 4px 20px;
    color: #fec502;
}

.login__wrap {
    margin: 40px;
}

.login__overlay {
    width: 100%;
    height: 50px;
    text-align: center;
    background-color: rgba(254, 197, 2, 1);
    border: none;
    line-height: 10px;
    border-radius: 15px;
    color: #fff;
    cursor: pointer;
    transition: all 0.3s ease 0s;
    text-decoration: none;
    font-family: 'Noto Sans KR', sans-serif;
    font-weight: 700;
}

.login__overlay:hover {
    background-color: rgba(255, 255, 255, 1);
    box-shadow: rgba(254, 197, 2, 0.3) 0px 4px 20px;
    color: #303030;
}

.menu-toggle {
    width: 25px;
    height: 25px;
}

/*
 * ------------------------------
 * Mobile Nav
 * ------------------------------
 */
.menu {
    display: none;
}

.overlay {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 10;
    right: 0;
    top: 0;
    background-color: rgba(255, 255, 255, 0.98);
    overflow-x: hidden;
    transition: all 0.5s ease 0s;
}

.overlay--active {
    width: 45%;
}

.overlay__content {
    display: flex;
    height: 80%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.overlay a {
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 20px;
    padding-bottom: 20px;
    font-size: 16px;
    display: block;
    transition: all 0.3s ease 0s;
}

.overlay a:hover,
.overlay a:focus {
    color: #fec502;
}
.overlay .close {
    position: absolute;
    left: 10px;
    font-size: 30px;
    color: #303030;
    cursor: pointer;
}

@media screen and (max-height: 450px) {
    .overlay a {
        font-size: 20px;
    }
    .overlay .close {
        font-size: 40px;
        left: 35px;
    }
}

@media only screen and (max-width: 1100px) {
    #mainNav {
        padding-left: 10px;
        padding-right: 10px;
    }
    .nav__links,
    .cta,
    .login {
        display: none;
    }
    .menu {
        display: initial;
    }
}
</style>
